<template>
  <div>
    <van-nav-bar title="我的收藏" @click-left="Return" class="bgc">
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>

    <div class="content">
      <van-card
        lazy-load
        v-for="(item,index) in allLike"
        :key="index"
        :price="item.price"
        :desc="item.desc"
        :title="item.name"
        :thumb="item.smallImg"
      >
        <template #tags>
          <van-tag plain type="danger">{{item.typeDesc}}</van-tag>
        </template>
        <template #footer>
          <van-button size="mini" @click="goDetail(item.pid)">再来一单</van-button>
          <van-button size="mini" @click="removeLike(index)">取消收藏</van-button>
        </template>
      </van-card>
    </div>
  </div>
</template>

<script>
import "../../assets/css/commaint.less";
export default {
  data() {
    return {
      // 收藏数据
      allLike: []
    };
  },
  created() {
    this.findAllLikeData();
  },
  methods: {
    // 返回
    Return() {
      this.$router.push({ name: "My" });
    },

    // 查询收藏商品数据
    findAllLikeData() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 2000) {
            this.allLike = res.data.result;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 取消收藏
    removeLike(index) {
      //获取token
      let tokenString = localStorage.getItem("__Tk");

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/notlike",
        data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 900) {
            this.like = res.data.msg;
            this.$toast(this.like);
            this.allLike.splice(index, 1);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    goDetail(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    }
  }
};
</script>

<style lang="less" scoped>
</style>